<!--  -->
<!--
aboutUs: ab
companyco
team-group:tg
-->
<template>
  <div class="ab">
    <coco-banner :bannerData="bannerData"></coco-banner>
    <!--  -->
    <div class="co w_960">
      <div class="co-left">
        <img src="/src/assets/images/aboutUs-list1.png" alt="co">
        <img src="/src/assets/images/aboutUs-list2.png" alt="co">
      </div>
      <div class="co-right">
        <h2 class="cor-title">
          <img :src="aboutUsData[0].abTitleUrl" alt="co">
        </h2>
        <p class="cor-text" v-html="aboutUsData[0].text">
        </p>
      </div>
    </div>
    <!--  -->
    <div class="tg">
      <div class="w_960">
        <div class="tg-left">
          <h2 class="tgr-title">
            <img :src="aboutUsData[1].abTitleUrl" alt="tg">
          </h2>
          <p class="tgr-text">
            {{ aboutUsData[1].text }}
          </p>
        </div>
        <div class="tg-right">
          <img src="/src/assets/images/abt-phone1.png" alt="tg">
          <img src="/src/assets/images/abt-phone2.png" alt="tg">
        </div>
      </div>
    </div>
    <coco-footer></coco-footer>
  </div>
</template>

<script type="text/ecmascript-6">
  import cocoFooter from '../../components/footer/footer.vue'
  export default {
    name: 'about-us',
    components: {
      cocoFooter
    },
    data() {
      return {
        bannerData: {
          isSwiper: false,
          info: {
            'imgUrl': '/src/assets/images/aboutUs.jpg',
            'content': {
              textDescBg: '',
              textDescSm: '-About CoCo-'
            }
          }
        },
        // 
        aboutUsData: [{
            abTitleUrl: '/src/assets/images/abc-title.png',
            text: `S2B<br><br>SAAS
              
              AI<br><br>
              <br><br>2017616
              -`
          },
          {
            abTitleUrl: '/src/assets/images/abt-title.png',
            text: `10APP107`
          }
        ]
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .ab
    .co
      margin 0 auto
      padding 81px 0 63px 0
      .co-left
        width 410px
        float left
        img
          float left
          height 316px
          &:first-child
            margin-right 25px
      .co-right
        float left
        width 550px
        padding-right 25px
        .cor-title
          padding-top 8px
          padding-bottom 22px
        .cor-text
          font-size 14px
          line-height 20px
          color rgb(51,51,51)
    .tg
      padding 100px 0
      width 100%
      background-color #f6f6f6
      .tg-left
        float left
        width 515px
        .tgr-title
          padding 20px 0 30px 0
        .tgr-text
          font-size 14px
          line-height 20px
          color rgb(51,51,51)
      .tg-right
        float left
        width 445px
        text-align right
        img
          &:first-child
            padding-right 60px
</style>




